import { makeAutoObservable } from 'mobx'
import { enList } from '@/assets/i18n/en'
import { zhList } from '@/assets/i18n/zh'

let defaultLang = 'en'
const langList = ['zh', 'en']
const localLang = window.localStorage.getItem('IMOLang')
const hrefArr = window.location.href.split('/')
const hrefSize = hrefArr.length
if (hrefSize > 0) {
  const endData = hrefArr[hrefSize - 1]
  // URL存在语言
  if (langList.includes(endData)) {
    if(defaultLang !== endData) defaultLang = endData
  } 
  // URL不存在语言
  else {
    if (localLang) {
      if(defaultLang !== localLang) defaultLang = localLang
    }
  }
} 

class Land {
  
  language = defaultLang

  en = enList

  zh = zhList

  constructor() {
    makeAutoObservable(this)
  }

  setLang(data) {
    if (this.language !== data) {
      this.language = data
      window.localStorage.setItem('IMOLang', data)
    }
  }
}

export default new Land()
